/* VARIABLES
-------------------------------------------------- */
:root {
  /* Grays */
  --gray-1: #fafaf9;
  --gray-2: #e7e5e4;
  --gray-3: #292524;
  --gray-4: #1c1917;
  --gray-m: #78716c;
}

/* STANDARDIZATIONS
-------------------------------------------------- */
html {
	box-sizing: border-box;
	font-size: 62.5%;
}

*, *:before, *::after {
	box-sizing: inherit;
}

html, body {
  min-height: 100dvh;
  padding: 0;
  margin: 0;
}

body{
	background: #f4f4f4;
	color: #555;
	font-family: Arial, Helvetica, sans-serif;
	font-size: 1.5rem;
	line-height: 1.6em;
  display: flex;
  flex-direction: column;
  align-items: stretch;
  justify-content: stretch;
  position: relative;
}

h1, h2, h3 {
	margin: 0;
}

h1 {
	font-size: 3rem;
	line-height: 1.15;
}

h3 {
	font-size: 2rem;
}

.std-btn {
  padding: 20px 30px;
  border-radius: 5px;
  border: none;
  background-color: var(--gray-m);
  color: #fff;
  font-size: 1.1em;
  font-weight: bold;
  letter-spacing: 2px;
}

.std-btn:hover {
  opacity: .8;
}

button:hover {
  cursor: pointer;
}

#content {
  flex: 1;
}

/* HEADER
-------------------------------------------------- */
header {
  position: sticky;
  top: -1px;
  display: flex;
  background: var(--gray-4);
  justify-content: space-between;
  padding: 25px 50px;
  align-items: center;
  transition: opacity .7s;
  z-index: 7;
}

header button {
  padding: 0;
  margin: 0;
  border: 0;
  background: none;
  display: flex;
}

header button:hover {
  font-weight: 600;
  cursor: pointer;
}

nav {
  display: flex;
  gap: 28px;
}

.header-title {
  font-weight: bold;
  letter-spacing: .2em;
}

h1.page-heading {
  font-size: 2.9em;
}

button.nav-btn {
  color: #fff;
  font-size: 1.1em;
  font-weight: 500;
  letter-spacing: .12em;
}

button.active-btn {
  font-weight: 600;
}

.is-pinned {
  transition: opacity .7s;
  opacity: .9;
}

hr {
  margin-left: 0;
  margin-right: 0;
  width: 50px;
}

/* CONNECT FOOTER
-------------------------------------------------- */
.connect-footer {
  display: flex;
  flex-direction: column;
  gap: 28px;
  align-items: center;
  padding: 50px 0px;
  background-color: var(--gray-2);
}

.connect-footer hr {
  margin: 0px 40%;
}

.connect-footer-top {
  display: flex;
  flex-direction: column;
  gap: 18px;
}

/* FOOTER
-------------------------------------------------- */
footer {
  display: flex;
  justify-content: space-around;
  padding: 50px 0px;
  background-color: var(--gray-4);
  color: var(--gray-2);
}